/* Introduce */
.introduce_div {
  display: grid;
  grid-template-columns: 350px auto 350px;
}

.introduce_div h2 {
  color: #23ac38;
  margin-bottom: 5px;
}

.introduce_div p {
  letter-spacing: -0.5px;
  color: #666;
}

.introduce_content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 10px;
  border-radius: 3px;
  border: gray 2px solid;
}

.introduce_content > div {
  margin: 10px;
}

/* Carousel */
.carousel_image {
  min-height: 528px;
}

/* Footer */
.footer_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  vertical-align: middle;
  background-color: #f5f5f5;
  height: 145px;
}

p {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.5px;
}

p:first-child {
  padding-top: 30px;
}

.footer_p {
  margin: 0;
}

.footer_copyright {
  margin: 0;
  color: gray;
}
